@font-face {
	font-family: "Neris";
	font-weight: 900;
	font-style: normal;
	font-display: swap;
	src:
		local("Neris"),
		local("Neris-Black"),
		url("../fonts/neris_black.woff") format("woff"),
		url("../fonts/neris_black.woff2") format("woff2");
}

